<template>
<div class="login-index">
    <div class="login-header">
        <img src="../../src/assets/logo.png" alt="">
    </div>
    <div class="login-mian">
        <el-row>
            <el-col :span="6" :offset="3">
                <div class="login-title">
                    卓望研发云系统
                </div>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm"  class="demo-ruleForm">
                    <el-form-item label="" prop="username">
                        <el-input placeholder="用户名" v-model="ruleForm.username" size="mini">
                            <template slot="prepend"><img src="../../src/assets/user.png" alt=""></template>
                        </el-input>
                    </el-form-item>  
                     <el-form-item label="" prop="password">
                        <el-input placeholder="密码" v-model="ruleForm.password" size="mini">
                            <template slot="prepend"><img src="../../src/assets/locked.png" alt=""></template>
                        </el-input>
                    </el-form-item>  
                        <el-form-item>
                            <el-button type="danger" class="btn" round @click="submitForm('ruleForm')">登录</el-button>
                        </el-form-item>
                    </el-form>
            </el-col>
             <el-col :span="9" :offset="3">
                <img src="../../src/assets/banner.png" alt="" width="100%">
            </el-col>
        </el-row>
    </div>
    <div class="login-footer">
       <el-row>
           <el-col :span="3" :offset="1" class="footer">
               <img src="../../src/assets/1.png" alt="" width="30%">
               代码托管
           </el-col>
           <el-col :span="1" class="footer1">
                <img src="../../src/assets/border.png" alt=""  width="3%">
           </el-col>
           <el-col :span="3" class="footer">
               <img src="../../src/assets/2.gif" alt="" width="30%">
               代码检查
           </el-col>
            <el-col :span="1" >
                <img src="../../src/assets/border.png" alt=""  width="3%">
           </el-col>
           <el-col :span="3" class="footer">
               <img src="../../src/assets/3.gif" alt="" width="30%">
               编译构建
           </el-col>
            <el-col :span="1">
                <img src="../../src/assets/border.png" alt=""  width="3%">
           </el-col>
           <el-col :span="3" class="footer">
               <img src="../../src/assets/4.gif" alt="" width="30%">
               测试管理
           </el-col>
            <el-col :span="1">
                <img src="../../src/assets/border.png" alt=""  width="3%">
           </el-col>
           <el-col :span="3" class="footer">
               <img src="../../src/assets/5.gif" alt="" width="30%">
               安全扫描
           </el-col>
            <el-col :span="1">
                <img src="../../src/assets/border.png" alt=""  width="3%">
           </el-col>
           <el-col :span="3" class="footer">
               <img src="../../src/assets/6.gif" alt="" width="30%">
               部署
           </el-col>
       </el-row>
    </div>
</div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
        username: "",
        password: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入正确的用户名", trigger: "blur" },
          { min: 3, max: 10, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入正确的密码", trigger: "blur" },
          { min: 3, max: 15, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$http
            .post("http://localhost/user/login", this.ruleForm)
            .then(response => {
              if (response.data.loin) {
                this.$router.push("/index");
              } else {
                this.$message({
                  duration: 1000,
                  message: "用户不存在或密码错误"
                });
              }
            })
            .catch(err => {
              console.log(err, "err错误");
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped="" type="text/css">
.login-index {
  height: 100%;
  & .login-header {
    height: 10%;
    padding: 1.3% 0 0 1.333333rem /* 100/75 */;
  }
  .login-mian {
    padding: 2% 0;
    & .login-title {
      font-size: 0.533333rem /* 40/75 */;
      margin-bottom: 10%;
      margin-top: 15%;
    }
    & .btn {
      padding: 0.133333rem /* 10/75 */ 0.533333rem /* 40/75 */;
      font-size: 12px;
      background: url("../../src/assets/btn.png") no-repeat;
      background-size: 100% 100%;
      border: none;
    }
  }
  .login-footer {
    height: 20%;
    // margin-top: 2.5%;
    text-align: center;
    & .footer {
      margin-top: 0.5%;
      height: 80%;
      img {
        margin-right: 3%;
      }
    }
  }
}
</style>




